import Topbar from '../../components/topbar.vue'
import {
	uniSwipeAction,
	uniSwipeActionItem,
	uniList,
	uniListItem
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			topbarData: {
				leftHandle: false,
				title: "订单填写",
				left: "《",
				right: ""
			},
			options: [{
				text: '更多日期 >',
				style: {
					backgroundColor: 'rgba(255,243,247,1)',
					color: '#FF9805'
				}
			}],
			pic: 1, // 票的张数
		};
	},
	onUnload(){
		uni.hideLoading()
	},
	
	methods: {
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		onClick(e) {
			// console.log('当前点击的是第'+e.index+'个按钮，点击内容是'+e.content.text)
			console.log(e.index)
		},
		change(open) {
			// console.log('当前开启状态：'+ open)
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			uni.showModal({
				content: '表单数据内容：' + JSON.stringify(formdata),
				showCancel: false
			});
		},
		formReset: function(e) {
			console.log('清空数据')
		},
		// 添加/减少票的张数
		changeNum(i){
			// i = 2 加 i = 1 减
			 if(i === 2) return this.pic++
			 if(i === 1 && this.pic > 1) return this.pic--
		}
	},
	components: {
		Topbar,
		uniSwipeAction,
		uniSwipeActionItem,
		uniList,
		uniListItem
	}
}
